<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td><code v-pre="">'https://api.mapbox.com/v4/'</code></td>
<td><code v-pre="">optional</code>指定 Mapbox 服务地址。</td>
</tr>
<tr>
<td>mapId</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>Mapbox 地图 ID。</td>
</tr>
<tr>
<td>accessToken</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>加载的 Mapbox 影像秘钥。</td>
</tr>
<tr>
<td>format</td>
<td>String</td>
<td><code v-pre="">'png'</code></td>
<td><code v-pre="">optional</code>请求返回的影像图片格式。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>参考椭球体，没指定的话默认 WGS84。</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code>最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>最大层级。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>图层的矩形范围,此矩形限制了影像可见范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>credit</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>服务描述信息。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/MapboxImageryProvider.html">MapboxImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者准备好时触发。</td>
</tr>
</tbody>
</table>
